import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'index',
    meta: {
      keepAlive: true
    },
    component: () =>
      import ('../page/index')
  }, {
    path: '/addService',
    name: 'addService',
    component: () =>
      import ('../page/addService')
  }, {
    path: '/vehicleTechnical',
    name: 'vehicleTechnical',
    component: () =>
      import ('../page/vehicleTechnical')
  }, {
    path: '/longRenting',
    name: 'longRenting',
    component: () =>
      import ('../page/longRenting')
  }, {
    path: '/shotRenting',
    name: 'shotRenting',
    component: () =>
      import ('../page/shotRenting')
  }, {
    path: '/rentedAndSale',
    name: 'rentedAndSale',
    component: () =>
      import ('../page/rentedAndSale')
  }, {
    path: '/fullCarSale',
    name: 'fullCarSale',
    component: () =>
      import ('../page/fullCarSale')
  }]
})



