import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    car_info: {},
    service_info: {},
    vehicle_info: {}
  },
  mutations: {
    "SET_CAR_INFO": function (state, car_info) {
      state.car_info = car_info
    },
    "SET_SERVICE_INFO": function (state, service_info) {
      state.service_info = service_info
    },
    "SET_VEHICLE_INFO": function (state, vehicle_info) {
      state.vehicle_info = vehicle_info
    }
  },
  getters: {
    "GET_CAR_INFO": function (state) {
      return state.car_info
    },
    "GET_SERVICE_INFO": function (state) {
      return state.service_info
    },
    "GET_VEHICLE_INFO": function (state) {
      return state.vehicle_info
    }
  },
  actions: {
    "SET_CAR_INFO": function (state, car_info) {
      store.commit("SET_CAR_INFO", car_info)
    },
    "SET_SERVICE_INFO": function (state, service_info) {
      store.commit("SET_SERVICE_INFO", service_info)
    },
    "SET_VEHICLE_INFO": function (state, vehicle_info) {
      store.commit("SET_VEHICLE_INFO", vehicle_info)
    }
  }
})



